<template>  
    <div id="home">
        <div id="usernamediv">
            <h1 id="usersname">{{username}}</h1>
        </div>
        <button id="deleteaccountbutton" style="display: none" v-on:click="openoverlay">Delete Account</button>

        <div id='overlay' style="display: none;">
            <div id="centeringdiv">
                <div id="deleteaccountdiv">
                    <h1 id="deleteaccounth1">Are you sure you want to delete your account?</h1>
                    <div id="buttonholders">
                        <input id="cancelbutton" type="button" value="Cancel" v-on:click='closeoverlay'>
                        <input id="deletebutton" type="button" value="DELETE ACCOUNT" v-on:click='deleteaccount'>
                    </div>
                </div>   
            </div>         
        </div>
    </div>
</template>

<script>
import userroutes from '../users.js'
import postroutes from '../posts.js'
import displaypostsuser from '../displaycreateuser.js'

export default {
    name: 'userpage',
    data() {
        return {
            username: this.$route.params.username,
            user: null,
            postids: null,
            posts: []
        }
    },
    async created() {
        try {
            this.user = await userroutes.getuser(this.username)
            this.postids = this.user.posts
            var i
            var newpost
            for(i = 0; i < this.postids.length; i++) {
                newpost = await postroutes.getpost(this.postids[i])
                this.posts.push(newpost)
            }
            var newdiv
            for(i = 0; i < this.posts.length; i++) {
                newdiv = displaypostsuser.displaypostsuser(this.posts[i])
                document.getElementById("home").appendChild(newdiv)
            }
            var userId = localStorage.getItem('userId')
            if(userId == this.user._id){
                document.querySelectorAll(".deletepostbutton").forEach(a=>a.style.display = "block")
                document.getElementById('deleteaccountbutton').style.display = 'block'
            } 

        } catch (err) {
            console.log(err)
            this.$router.push({path: '/login'})
        }
    },
    methods: {
        openoverlay() {
            document.getElementById('overlay').style.display = 'block'
        },

        closeoverlay() {
            document.getElementById('overlay').style.display = 'none'
        },

        async deleteaccount() {
            var userId = localStorage.getItem('userId')
            if(this.user._id == userId) {
                await userroutes.deleteuser(localStorage.getItem(''))
                localStorage.removeItem('token')
                localStorage.removeItem('userId')
                localStorage.removeItem('username')
                this.$router.push({path: '/login'})
            } else {
                console.log("Cannot delete account")
            }
            
        }
    }
}
</script>

<style>

#usernamediv {
    border-bottom: 2px solid;
    border-bottom-color: black;
    width: 50rem;
}

#usersname {
    color: black;
}

#deletebuttondiv {
    display: flex;
    justify-content: flex-end;
    width: 640px;
}

.deletepostbutton {
    font-size: 10px;
}

#overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
}

#centeringdiv {
    width: 100%;
    display: flex;
    justify-content: center;
}

#deleteaccounth1 {
    color: black;
}

#deleteaccountdiv {
    margin-top: 100px;
    background-color: rgb(228, 225, 225);
    width: 60rem;
    border-radius: 5px;
    box-shadow: 1px 7px 20px 2px rgb(0 0 0 / 30%);
}

#deletebutton {
    width: 140px;
    height: 50px;
    margin-left: 10px;
    border-radius: 5px;
    border-color: rgb(220, 233, 245);
    background-color: rgb(133, 4, 4);
    
}
</style>