import axios from 'axios'

const url = 'http://localhost:5000/user/'

class userroutes {
    static getuser(username) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${url}${username}`, headers: {Authorization: `Bearer ${token}`}, method: 'GET'}).then((result) => {
                resolve(result.data)
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }
    
    static deleteuser(username) {
        return new Promise((resolve, reject) => {
            const token = localStorage.getItem('token')
            axios({url: `${url}${username}/delete`, headers: {Authorization: `Bearer ${token}`}, method: 'DELETE'}).then(() => {
                resolve('Success')
            }).catch((err) => {
                console.log(err)
                reject(err)
            })
        })
        
    }
}


export default userroutes